<template>
    <div class="plug">
        <div class="clock">   
            <p class="time">{{time}}</p>
             <div class="btn btn-outline-primary" @click="getStart">计时开始 </div>
             <div class="btn btn-outline-primary" @click="getEnd">计时结束  </div>
        </div>
        <div class="addErr" @click="getErr"> {{error}} </div>
        <ul>
        	<li v-for="(item,index) in arr" :key="index">
        			<i class="order">{{index+1}}、</i> 
        			<b class="btns">{{item.n1}}</b> + <b class="btns">{{item.n2}} </b>  = <b class="btns btna"   @click="getAnser(index)"><b v-show="item.n3 == 1" class="ddd">	{{item.n1+item.n2}}</b> </b>
        	</li> 
        </ul>
         
    </div>
</template>

<script>
export default {
    name: 'plug',
    data () {
        return { 
        	arr:[],
        	num:[] ,
             time:'', 
            error:0,
        }
    },
    created(){
    	for(let i =0;i<50;i++){
    		let o={};
    		o.n1= Math.ceil(Math.random()*(10-3+1)+1);
    		o.n2= Math.ceil(Math.random()*(10-3+1)+1);
    		o.n3=0;
    		this.arr.push(o); 
    	}
    	console.log(this.arr)
    },
    methods:{
    	getAnser(index){   
    		this.arr[index].n3=1 
    	},
         getErr(){
            this.error ++;
        },
        getEnd(){
            clearInterval(timer);
        },
        getStart(){   
                let start = new Date();
                let sec = 0;
                let min = 0;
                let hour = 0;
                let str = '';
                timer = setInterval(()=>{  
                    sec++;
                    if(sec == 60){
                        sec = 0;
                        min += 1;
                    }
                    if(min == 60){
                        hour += 1;
                        min = 0;
                    }  
                    str = hour +":"+min+":"+sec;
                    this.time = str;
                },1000)  
            
        },
    }

}
</script>

