import Vue from 'vue'
import Router from 'vue-router' 
import plug from '@/components/plug'
import minus from '@/components/minus' 
import multiPlug from '@/components/multiPlug' 

Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/',
        name: 'index',
        component: plug 
    },
  	{
        path: '/plug',
        name: 'plug',
        component: plug 
    },
    {
        path: '/minus',
        name: 'minus',
        component: minus 
    },
    {
        path: '/multiPlug',
        name: 'multiPlug',
        component: multiPlug 
    } 
 
  ],
  mode:'hash'
})



